import React, { Component } from "react";
import {
  CButton,
  CFormGroup,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CLabel,
  CInput,
  CSelect,
} from "@coreui/react";
import api from "../../api";
import authHeader from "../../auth-header";

import Toast from "light-toast";
import { ScaleLoader } from "react-spinners";
import { css } from "@emotion/core";
const override = css`
  display: block;
  border-color: black;
  position: absolute;
  left: 43%;
  top: 50%;
  z-index: 1;
`;

class AddCar extends Component {
  constructor(props) {
    super(props);

    this.handleChange = this.handleChange.bind(this);
    this.carSubmit = this.carSubmit.bind(this);
    this.cancel = this.cancel.bind(this);

    this.state = {
      loading: false,
      brands: [],

      brand: "",
      name: "",

      brandError: "",

      nameError: "",
    };
    const { data } = this.props.location;
    if (data !== undefined) {
      localStorage.setItem("car_id", data);
    }
  }

  handleChange = (input) => (e) => {
    // console.log(input);
    // console.log(e.target.value);
    this.setState({ [input]: e.target.value });
    this.setState({ [input + "Error"]: "" });
  };

  componentDidMount() {
    this.setState({ loading: true });
    fetch(api.apiUrl + "get_brand", {
      method: "POST",
      headers: authHeader(),
    })
      .then((response) => response.json())
      .then((responseJsonFromServerData) => {
        this.setState({ brands: responseJsonFromServerData, loading: false });
      })
      .catch((error) => {
        console.error(error);
        this.setState({ loading: false });
      });

    //fetch data
    var data_id = localStorage.getItem("car_id");
    var con = {
      id: data_id,
    };
    this.setState({ loading: true });
    fetch(api.apiUrl + "fetch_car_by_id", {
      method: "POST",
      headers: authHeader(),
      body: JSON.stringify(con),
    })
      .then((response) => response.json())
      .then((responseJsonFromServerData) => {
        console.log(responseJsonFromServerData);
        if (responseJsonFromServerData.length !== 0) {
          responseJsonFromServerData.map((item, key) => {
            this.setState({
              brand: item.brandId,
              name: item.name,
            });
          });
        } else {
          Toast.fail("failed...", 600, () => {
            this.props.history.push("/cars");
          });
        }
        this.setState({ loading: false });
      })
      .catch((error) => {
        console.error(error);
        this.setState({ loading: false });
      });
  }

  carSubmit = () => {
    var data_id = localStorage.getItem("car_id");

    if (this.state.name === "") {
      this.setState({ nameError: "Enter Car Name" });
    }

    if (this.state.brand === "" || this.state.brand === "select") {
      this.setState({ brandError: "Select Brand" });
    }

    if (
      this.state.name !== "" &&
      this.state.brand !== "" &&
      this.state.brand !== "select"
    ) {
      var data_arr = {
        name: this.state.name,
        brand: this.state.brand,
        id: data_id,
      };

      this.setState({ loading: true });
      fetch(api.apiUrl + "update_car", {
        method: "POST",
        headers: authHeader(),
        body: JSON.stringify(data_arr),
      })
        .then((response) => response.json())
        .then((responseJsonFromServerData) => {
          // console.log(responseJsonFromServerData);
          if (responseJsonFromServerData === "success") {
            //   console.log("sucess1");
            Toast.success("Updated successfully...", 600, () => {
              // do something after the toast disappears
              this.props.history.push({
                pathname: "/cars",
                data: 1,
              });
            });
          } else if (responseJsonFromServerData === "failed") {
            Toast.fail("failed...", 600, () => {
              // do something after the toast disappears
            });
          }
          this.setState({ loading: false });
        })
        .catch((error) => {
          console.error(error);
          this.setState({ loading: false });
        });
    }
  };

  cancel = () => {
    this.props.history.push("/cars");
  };
  render() {
    return (
      <>
        <CRow>
          <CCol>
            <CCard>
              <ScaleLoader
                height={40}
                width={12}
                css={override}
                size={150}
                //size={"150px"} this also works
                color={"#B54545"}
                loading={this.state.loading}
              />
              <CCardHeader>
                {" "}
                <CRow>
                  <CCol sm="6" xs="6" md="8">
                    <h3>Edit Car</h3>
                  </CCol>
                  <CCol sm="6" xs="6" md="4"></CCol>
                </CRow>
              </CCardHeader>
              <CCardBody>
                <CRow>
                  <CCol md="3" sm="3" xs="1"></CCol>
                  <CCol md="6" sm="6" xs="10">
                    <form id="carForm">
                      <CFormGroup>
                        <CLabel htmlFor="name">Name</CLabel>

                        <CInput
                          autoFocus
                          type="text"
                          value={this.state.name}
                          placeholder="Car Name"
                          onChange={this.handleChange("name")}
                          style={{
                            borderColor:
                              this.state.nameError === "" ? "" : "red",
                          }}
                        />
                      </CFormGroup>
                      <p
                        className="error"
                        style={{
                          display:
                            this.state.nameError === "" ? "none" : "block",
                        }}
                      >
                        <i className="fa fa-exclamation-triangle"></i>{" "}
                        {this.state.nameError}
                      </p>

                      <CFormGroup>
                        <CLabel htmlFor="brand">Brand</CLabel>

                        <CSelect
                          custom
                          value={this.state.brand}
                          onChange={this.handleChange("brand")}
                          style={{
                            borderColor:
                              this.state.brandError === "" ? "" : "red",
                          }}
                        >
                          <option value="select">Select Brand</option>
                          {this.state.brands.map((item, key) => {
                            return (
                              <option key={key} value={item.id}>
                                {item.name}
                              </option>
                            );
                          })}
                        </CSelect>
                      </CFormGroup>
                      <p
                        className="error"
                        style={{
                          display:
                            this.state.brandError === "" ||
                            this.state.brandError === "select"
                              ? "none"
                              : "block",
                        }}
                      >
                        <i className="fa fa-exclamation-triangle"></i>{" "}
                        {this.state.brandError}
                      </p>
                      <CCol md="12" sm="12" xs="12" className="text-center">
                        <CButton
                          className="px-4 cancel-btn"
                          onClick={() => this.cancel()}
                        >
                          Cancel
                        </CButton>
                        <CButton
                          className="px-4 submit-btn"
                          onClick={this.carSubmit}
                        >
                          Submit
                        </CButton>
                      </CCol>
                    </form>
                  </CCol>
                </CRow>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </>
    );
  }
}

export default AddCar;
